  padding-right: 10px;
}

.card-description {
  font-size: 20px;
}

.home-nav {
  grid-area: home-nav;
  display: flex;
  align-items: center;
  background-color: #a9a9a9;
  font-family: Futura, sans-serif;
}

.home-logo {
  cursor: pointer;
