  grid-template:
    "img  .     " 40%
    "img addr  " 20%
    "img star  " 20%
    "img endt  " 20%
    / 40% 60%;
}

.cardview-image-appointment {
  justify-self: left;
  grid-area: img;
  max-width: 100%;
  max-height: 100%;
  height: 190px;
}

.appointment-address {
  grid-area: addr;
}

.start-time-appointment {
  grid-area: star;
}

.end-time-appointment {
  grid-area: endt;
}

.my-appointment-body {
  font-family: "Futura", sans-serif;
  display: flex;
  flex-direction: column;
  width: 1200px;
  align-items: center;
  font-size: 18px;
}

.cardView-appointment {
  margin-top: 20px;
}

.appointment-title {
  font-family: "Futura-light", sans-serif;
  font-weight: 600;
  margin-top: 20px;
  margin-left: 570px;
  font-family: ;
}

.home-container {
  display: grid;
  background: linear-gradient(rgba(0, 0, 0, 0.3), rgba(0, 0, 0, 0.5)),
    url(/static/media/map.cfb3cb6a.gif) center center / cover no-repeat;

  grid-template:
    "home-nav home-nav" 2%
    "activeTitle ." 2%
    "cardview calendar" 20%
    ". .       " 75%
    / 25% 75%;

  grid-gap: 10px;
  grid-row-gap: 20px;
}

.home-nav {
  grid-area: home-nav;
}

.home-active-post-title {
  grid-area: activeTitle;
  justify-self: center;
  color: #808080;
  font-family: "Montserrat", sans-serif;
  margin-top: 20px;
}

.home-cardview {
  grid-area: cardview;
  overflow: scroll;
  overflow-y: auto;
  padding: 50px;
  border: 4px solid #a9a9a9;
  border-radius: 15px;
  margin-left: 30px;
}

.home-calendar {
  grid-area: calendar;
  overflow: scroll;
  overflow-y: auto;
  padding-left: 40px;
  padding-right: 40px;
  padding-bottom: 40px;
  padding-top: 20px;
  justify-content: center;
  border: 4px solid #a9a9a9;
  border-radius: 15px;
  font-size: 15px;
  font-family: "Montserrat", sans-serif;
  margin-right: 30px;
}

li,
a,
button {
  font-weight: 500;
}

